import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { FormBuilder, FormGroup, FormArray } from '@angular/forms';

// models
import { IAccounts } from './../../../../core/models/IAccounts';
import { AccountsClass } from './../../../../core/models/Accounts';

// services
import { AccountsService } from './../../../../core/services/accounts.service';

@Component({
  selector: 'accounts-list',
  templateUrl: './accounts-list.component.html',
  styleUrls: ['./accounts-list.component.scss']
})
export class AccountsListComponent implements OnInit, OnDestroy {

  private _onChooseAllEvent;
  @Input() set onChooseAllEvent(value) { 

    if (value) {
      this.chooseAllEvent(value);
      this._onChooseAllEvent = value; 
    }
  }
  get onChooseAllEvent() { 
    return this._onChooseAllEvent 
  }

  @Input() isAll = false;

  @Output() onChooseAll = new EventEmitter<boolean>();

  public accounts$: Observable<IAccounts[]>;
  public accounts: AccountsClass;
  public accountForm: FormGroup;

  constructor(
    private fb: FormBuilder,
    private accountsService: AccountsService
  ) {
    this.accounts = this.accountsService.accounts;
  }

  ngOnInit() {
    this.getAccounts();
  }

  ngOnDestroy() {
    this.accounts.clearAll();
  }

  /**
   * get all accounts from API and get accounts
   */
  private getAccounts():void {
    this.accounts$ = this.accountsService.getAccounts(this.isAll).pipe(
      map(response => {
        this.accounts.setAccounts(response.data.assets);
        this.createAccountForm();
        return response.data;
      })
    )
  }

  /**
   * create account list form
   */
  private createAccountForm(): void {
    this.accountForm = this.fb.group({
      accounts: this.buildAccounts()
    });
  }

  /**
   * get list of controls from accounts form
   */
  get accountControls() {
    return this.accountForm.get('accounts') as FormArray;
  };

  /**
   * add accounts list to FormArray controls
   */
  private buildAccounts() {
    const array = this.accounts.getAccounts().map(() => {
      return this.fb.control(false);
    });
    return this.fb.array(array);
  }

  /**
   * manage local account list 
   */
  public onCheckboxChanges(event, account): void {
    if (!event.target.checked) {
      this.accounts.removeCheckedAccount(account.asset_unique_id);
      console.log('on deselect')
      this.onChooseAll.emit(false);
    }

    if (event.target.checked) {
      this.accounts.setCheckedAccount(account);
    }
  }

  /**
   * choose all accounts
   */
  public chooseAllEvent(event): void {
    this.accountControls.controls.map(control => {
      control.setValue(event.target.checked);
    });

    this.accounts.setCheckedAccounts(
      event.target.checked ? [...this.accounts.getAccounts()] : []
    );
  }

}
