import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { MatDialog } from '@angular/material';

// components
import { TermsConditionsPopupComponent } from '../../../../popups/terms-conditions-popup/terms-conditions-popup.component';

// models
import { IUser } from '../../../../../../core/models/IUser';

// services
import { HelpersService } from '../../../../../../core/services/helpers.service';

@Component({
  selector: 'user-profile',
  templateUrl: './user-profile.component.html',
  styleUrls: ['./user-profile.component.scss']
})
export class UserProfileComponent implements OnInit {

  @Input() userProfile: IUser;
  @Output() private editProfileEvent: EventEmitter<null> = new EventEmitter<null>();
  profileImageUrl: any;
  constructor(
    public helpers: HelpersService,
    private dialog: MatDialog
  ) { }

  ngOnInit() { 
    if (this.userProfile.profile_pic == null || this.userProfile.profile_pic == undefined || this.userProfile.profile_pic.trim() == '') {
      this.profileImageUrl = './../../../../../../../assets/images/round-img.png';
    } else {
      this.profileImageUrl = 'https://s3.amazonaws.com/' + this.userProfile.profile_pic;
    }
  }

  /**
   * change user profile view to edit mode 
   */
  public editProfile(): void {
    this.editProfileEvent.next();
  }

  /**
   * show terms and conditions popup
   */
  public showTermsConditions(): void {
    this.dialog.open(TermsConditionsPopupComponent, {
      panelClass: "popup-screen",
      disableClose: true,
      width: "1200px",
      autoFocus: false
    });
  }

  /**
   * check if fields  is empty
   */
  public addressLines(fields: string[]): string {
    let string = ''; 
    
    fields.forEach((item, index) => {
      if (item && item.length > 0) {
        string = string + (item +  (fields.length - 1 === index ? '' : ', '));
      }
    });

    return string;
  }
}
