// 3rd party ibs
import { Component, OnInit, Input } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { map, takeUntil } from 'rxjs/operators';
import { MatDialog } from '@angular/material';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';

// services
import { BeneficiariesService } from '../../../../../core/services/beneficiaries.service';
import { HelpersService } from '../../../../../core/services/helpers.service';
import { ToasterService } from '../../../../../services/toaster.service';

// components
import { AddBeneficiariesPopupComponent } from '../../../popups/add-beneficiaries-popup/add-beneficiaries-popup.component';

// models
import { IContacts } from '../../../../../core/models';
import { BeneficiariesClass } from '../../../../../core/models/BeneficiariesClass';

@Component({
  selector: 'app-beneficiaries-block',
  templateUrl: './beneficiaries-block.component.html',
  styleUrls: ['./beneficiaries-block.component.scss']
})
export class BeneficiariesBlockComponent implements OnInit {

  public lock = false;
  @Input() public accountId: number;
  public beneficiaries$: Observable<IContacts[]>
  public beneficiaries: BeneficiariesClass = null;
  public amount: boolean;
  private destroy$: Subject<boolean> = new Subject<boolean>();

  constructor(
    public helpers: HelpersService,
    private dialog: MatDialog,
    private toasterService: ToasterService,
    private beneficiariesService: BeneficiariesService,
  ) {
    this.beneficiaries = this.beneficiariesService.beneficiaries;
  }

  ngOnInit() {
    if (this.accountId && +this.accountId === -1) {
      this.getBeneficiariesForAllAccounts();
      return;
    }

    if (this.accountId) {
      this.beneficiaries$ = this.getBeneficiaries();
    }
  }

  ngOnDestroy() {
    this.destroy$.next(true);
    this.destroy$.complete();
    this.beneficiaries.clearAll();
  }

  /**
   * init beneficiaries list for assign beneficiaries page
   */
  private getBeneficiariesForAllAccounts(): void {
    this.beneficiaries$ = Observable.of([]);
    this.beneficiaries.setBeneficiaries([]);
  }

  /**
   * get all beneficiaries for current account
   */
  private getBeneficiaries(): Observable<any> {
    return  this.beneficiariesService
      .getBeneficiariesByAccount(this.accountId)
      .pipe(
        map(value => {
          this.beneficiaries.setBeneficiaries(value.data);
          this.amount = !!this.beneficiaries.getBeneficiaries().length;
          return value.data;
        })
      );
  }

  /**
   * remove from local list of beneficiaries
   */
  public removeFromList(caretakerId: number): void {
    this.beneficiaries.removeBeneficiary(caretakerId);
  }

  /**
   * change beneficiaries order
   */
  public changeOrder(event: CdkDragDrop<IContacts[]>): void {
    const currentList = this.beneficiaries.getBeneficiaries();

    moveItemInArray(currentList, event.previousIndex, event.currentIndex);
    this.beneficiaries.setBeneficiaries(currentList);
  }

  /**
   * save list of beneficiaries
   */
  public saveBeneficiaries(): void {
    this.lock = true;
    const forSaving = { beneficiaries: [] };
    const currentBeneficiaries = this.beneficiaries.getBeneficiaries();

    currentBeneficiaries.forEach((contact, index) => {
      forSaving.beneficiaries.push({
        contactId: contact.user_caretaker_id,
        priority: index + 1
      });
    });

    this.beneficiariesService.updateBeneficiariesByAccount(this.accountId, forSaving)
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
      this.lock = false;
      this.toasterService.showSuccess('Beneficiaries has been updated');
    }, () => {
      this.lock = false;
      this.toasterService.showError('Something went wrong');
    })
  }

  /**
   * contacts list popup
   */
  public addBeneficiaries(): void {
    this.dialog.open(AddBeneficiariesPopupComponent, {
      panelClass: "popup-screen",
      disableClose: true,
      width: "600px",
      autoFocus: false
    });
  }

}
