import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subject, Observable } from "rxjs";
import { map, takeUntil } from 'rxjs/operators';

// models
import { IUser } from '../../../../../core/models/IUser';

// services
import { AuthService } from '../../../../../services/auth.service';
import { EstatePlanService } from '../../../../../core/services/estate-plan.service';
import { ProfileService } from './../../../../../core/services/profile.service';

@Component({
  selector: 'user-profile-block',
  templateUrl: './user-profile-block.component.html',
  styleUrls: ['./user-profile-block.component.scss']
})
export class UserProfileBlockComponent implements OnInit, OnDestroy {

  public userProfile: IUser;
  public userProfile$: Observable<IUser>;
  public isEditUser = false;
  public lock = false;
  private destroy$: Subject<boolean> = new Subject<boolean>();

  constructor(
    private authService: AuthService,
    private profileService: ProfileService,
    private estatePlanService: EstatePlanService,
  ) { }

  ngOnInit() {
    this.getUserProfile();
  }

  ngOnDestroy() {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  /**
   * get user profile method
   */
  private getUserProfile(): void {
    this.userProfile$ = this.authService.getUserProfile().pipe(
      map(value => {
        this.userProfile = value.data;
        return value;
      })
    )
  }

  /**
   * save user profile
   */
  public saveUserProfile(userProfile: IUser): void {
    this.lock = true;

    const currentUserProfile = {
      ...userProfile,
      estatePlanTC: userProfile.estatePlanTC ? 'YES' : 'NO'
    };

    this.profileService.updateUserProfile(currentUserProfile)
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        this.lock = false;
        this.isEditUser = !this.isEditUser;
        this.userProfile = currentUserProfile;
        console.log(this.userProfile, 'on save')
      }, () => {
        this.lock = false;
      });
  }
}
