import { Component, OnInit } from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { MatDialogRef } from '@angular/material';

// services
import { AccountsService } from '../../../../core/services/accounts.service';
import { ToasterService } from '../../../../services/toaster.service';

// models
import { AccountsClass } from '../../../../core/models/Accounts';

@Component({
  selector: 'app-add-accounts-popup',
  templateUrl: './add-accounts-popup.component.html',
  styleUrls: ['./add-accounts-popup.component.scss']
})
export class AddAccountsPopupComponent implements OnInit {

  public chooseAllEvent;
  public accounts: AccountsClass;
  public estatePlan: AccountsClass;
  public lock = false;
  private destroy$: Subject<boolean> = new Subject<boolean>();
  
  constructor(
    private accountsService: AccountsService,
    private toasterService: ToasterService,
    public dialogRef: MatDialogRef<AddAccountsPopupComponent>,
  ) { 
    this.accounts = this.accountsService.accounts
    this.estatePlan = this.accountsService.estatePlan;
  }

  ngOnInit() { }

  ngOnDestroy() {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  onChooseAll($event){
    this.chooseAllEvent.target.checked = false;
  }
  /**
   * add accounts to estate plan request to API
   */
  public addToEstatePlan(): void {
    this.lock = true;
    const forSaving = { accounts: [] };

    this.accounts.getCheckedAccounts().forEach((contact) => {
      forSaving.accounts.push(contact.asset_unique_id);
    });

    this.accountsService.addAccountsToEstatePlan(forSaving)
      .pipe(takeUntil(this.destroy$))
      .subscribe(response => {
      this.lock = false;
      this.convertToEstatePlanFormat();
      console.log(response);
      this.estatePlan.setEstatePlanData(response.data.isComplete);
      this.toasterService.showSuccess('Accounts has been added to estate plan');
      this.dialogRef.close();
    }, () => {
      this.lock = false;
      this.toasterService.showError('Something went wrong');
    });
  }

  /**
  * convert raw accounts array to appropriate format for estate table accounts table
  * and add these new accounts to estate plan table locally
  */
  private convertToEstatePlanFormat(): void {
    const accounts = this.accounts.getCheckedAccounts().map(value => {
      const {
        asset_unique_id,
        asset_account_num,
        asset_title,
        asset_group_title,
        asset_url,
        asset_notes
      } = value;

      return {
        asset_unique_id,
        asset_group_title,
        asset_title,
        asset_account_num,
        asset_url,
        asset_notes,
        beneficiaries: []
      }
    });
    this.estatePlan.concatAccounts(accounts);
  }
}
