import { Component, OnInit, Input, Output, EventEmitter, ChangeDetectionStrategy, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material';
import {  Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

// components
import { ProfileService } from '../../../../../../core/services/profile.service';
import { TermsConditionsPopupComponent } from '../../../../popups/terms-conditions-popup/terms-conditions-popup.component';

// models
import { IUser, ICountries, IStates } from '../../../../../../core/models';

@Component({
  selector: 'edit-user-profile',
  templateUrl: './edit-user-profile.component.html',
  styleUrls: ['./edit-user-profile.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class EditUserProfileComponent implements OnInit, OnDestroy {

  public profileForm: FormGroup;
  public states: IStates[] = [];
  public countries: ICountries[] = [];
  @Input() userProfile: IUser;
  @Output() private saveUserProfileEvent: EventEmitter<IUser> = new EventEmitter<IUser>();
  private destroy$: Subject<boolean> = new Subject<boolean>();

  zipPattern = "^[0-9]{5}(-[0-9]{4})?$";
  constructor(
    private fb: FormBuilder,
    private profileService: ProfileService,
    private dialog: MatDialog
  ) { }

  ngOnInit() {
    if (this.userProfile) {
      this.createProfileForm();
    }
    this.getCountries();
  }

  ngOnDestroy() {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  /**
   * get countries list from API
   */
  private getCountries(): void {
    this.profileService.getCountries()
      .pipe(takeUntil(this.destroy$))
      .subscribe(counties => {
        this.countries = counties.data;

        if (this.userProfile.user_country.id) {
          this.getStates(+this.userProfile.user_country.id, +this.userProfile.user_state.id);
          this.profileForm.get('user_country').setValue(+this.userProfile.user_country.id, { emitEvent: false });
        }
      });
  }

  /**
   * get states list from API
   */
  private getStates(countryId: number, value: number): void {
    this.profileService.getStates(countryId)
      .pipe(takeUntil(this.destroy$))
      .subscribe(counties => {
        this.states = counties.data;
        this.profileForm.get('user_state').setValue(value);
      });
  }

  /**
   * create user profile form
   */
  private createProfileForm(): void {
    const reg = /^(\d{3})-?(\d{3})-?(\d{4})/;
    // const  zipPattern = "^[0-9]{5}(-[0-9]{4})?$";

    delete this.userProfile.isEncrypted;
    delete this.userProfile.onboard_complete;
    delete this.userProfile.tartuphelpenabled;
    delete this.userProfile.id;
    delete this.userProfile.startuphelpenabled;
    console.log(this.userProfile);
    this.profileForm = this.fb.group({
      ...this.userProfile,
      user_country: [this.userProfile.user_country, Validators.required],
      user_state: [this.userProfile.user_state, Validators.required],
      user_zip_code: [this.userProfile.user_zip_code, Validators.required],
      user_phone1: [this.userProfile.user_phone1, Validators.pattern(reg)],
      user_phone2: [this.userProfile.user_phone2, Validators.pattern(reg)],
      estatePlanTC: this.userProfile.estatePlanTC === 'NO' ? false : true
    });

    /**
     * load list of states if country was changed
     */
    this.profileForm.get('user_country').valueChanges
      .pipe(takeUntil(this.destroy$))
      .subscribe(countryId => {
        if(countryId == 1){
          this.zipPattern = "^[0-9]{5}(-[0-9]{4})?$";
        } else if(countryId == 2){
          this.zipPattern = "^([A-Z][0-9][A-Z] ?[0-9][A-Z][0-9])$";
        } else if(countryId == 3){
          this.zipPattern ="^[1-9][0-9]{5}$"
        } else if(countryId == 4){
          this.zipPattern ="^[0-9]{4}$"
        } else {
          this.zipPattern = "^[a-zA-Z0-9_]*$";
        }

        this.getStates(countryId, null);
      });
  }

  /**
   * change edit mode to user profile
   */
  
  public saveUserProfile(): void {
    console.log(this.profileForm.value.user_phone1);
    console.log(this.profileForm.valid);
    
    const fieldsForSaving = { ...this.profileForm.value };
    fieldsForSaving.user_country = this.countries.find(country => country.id === fieldsForSaving.user_country);
    fieldsForSaving.user_state = this.states.find(state => state.id === fieldsForSaving.user_state);

    this.saveUserProfileEvent.next(fieldsForSaving);
  }

  /**
   * show terms and conditions popup
   */
  public showTermsConditions(): void {
    this.dialog.open(TermsConditionsPopupComponent, {
      panelClass: "popup-screen",
      disableClose: true,
      width: "1200px",
      autoFocus: false
    });
  }
}
