import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';

// components
import { EstatePlanPageComponent } from './pages/estate-plan-page/estate-plan-page.component';
import { SingleAssetPageComponent } from './pages/single-asset-page/single-asset-page.component';
import { AssignBeneficiariesPageComponent } from './pages/assign-beneficiaries-page/assign-beneficiaries-page.component';

const routes: Routes = [
  { path: '', component: EstatePlanPageComponent },
  { path: 'new', component: SingleAssetPageComponent },
  { path: 'edit/:id', component: SingleAssetPageComponent },
  { path: 'assign-beneficiaries', component: AssignBeneficiariesPageComponent }
]

@NgModule({
  declarations: [],
  imports: [
    CommonModule,
    RouterModule.forChild(routes)
  ]
})
export class EstatePlanRoutingModule { }
