// 3rd party libs
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Subject, Observable, forkJoin } from "rxjs";
import { takeUntil, map } from "rxjs/operators";

// models
import { IDrawersGroup, IPasswordHint } from '../../../../core/models';

// services
import { PasswordHintService } from '../../../../services/passwordHintService/passwordhint.service';
import { AssetService } from '../../../../services/assetService/asset.service';
import { ToasterService } from '../../../../services/toaster.service';
import { AccountsService } from './../../../../core/services/accounts.service';

// copy
import { staticText } from '../../../../../assets/static-data/staticText';

interface ISingleAccount {
  id: number;
  title: string;
  accountNumber: string;
  passwordHintId: number;
  asset_group_id: number;
  url: string;
  notes: string;
  location: string;
  fileLink: string;
  estatePlan: string;
}

@Component({
  selector: 'single-asset-page',
  templateUrl: './single-asset-page.component.html',
  styleUrls: ['./single-asset-page.component.scss']
})
export class SingleAssetPageComponent implements OnInit {

  public initFormData$ = new Observable;
  public assetId: number;
  public lock = false;
  public staticText;
  public assetForm: FormGroup;
  public drawersGroup: IDrawersGroup[];
  public passwordHints: IPasswordHint[];
  private destroy$: Subject<boolean> = new Subject<boolean>();

  constructor(
    private assetService: AssetService,
    private accountsService: AccountsService,
    private passwordHintService: PasswordHintService,
    private toasterService: ToasterService,
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private router: Router
  ) {
    this.assetId = +this.route.snapshot.paramMap.get('id');
    this.staticText = staticText;
  }

  ngOnInit() {
    this.getDataForForm();
  }

  ngOnDestroy() {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  /**
   * get all data for init form
   */
  private getDataForForm(): void {
    const getPasswordHints = this.passwordHintService.getPasswordHints();
    const getAssetGroup = this.assetService.getDrawersGroup();
    const requests = [getPasswordHints, getAssetGroup];

    if (this.assetId) {
      const getAccount = this.accountsService.getAccount(this.assetId)
      requests.push(getAccount);
    }

    this.initFormData$ = forkJoin(requests).pipe(
      map(response => {
        this.passwordHints = response[0].data;
        this.drawersGroup = response[1].data;
        this.createAssetForm();

        if (response[2]) {
          this.addUserToForm(response[2].data);
        }

        return response;
      })
    )
  }

  /**
   * create estate plan asset form
   */
  private createAssetForm(): void {
    this.assetForm = this.fb.group({
      asset_title: [null, Validators.required],
      asset_group_id: [null, Validators.required],
      asset_account_num: '',
      PasswordHint_id: null,
      asset_url: '',
      asset_notes: '',
      asset_location: '',
      asset_link_to_file: '',
      estatePlan: 'YES'
    });

    if (this.assetId) {
      this.assetForm.get('asset_group_id').disable();
    }
  }

  /**
   * update account form if we edit account
   * @param account single account data
   */
  private addUserToForm(account: ISingleAccount): void {
    this.assetForm.patchValue({
      asset_title: account.title,
      asset_notes: account.notes,
      asset_group_id: this.drawersGroup.find(hint => 
        hint.ag_id === account.asset_group_id).ag_id,
      PasswordHint_id: account.passwordHintId === 0 ? null :
        this.passwordHints.find(hint => 
          hint.pwd_hint_id === account.passwordHintId).pwd_hint_id,
      asset_url: account.url,
      asset_account_num: account.accountNumber
    });
  }
  
  /**
   * back to the estate plan list
   */
  public backToList(): void {
    this.router.navigate(['/estate-plan']);
  }

  /**
   * action on submit form button 
   */
  public submitForm(action = 'save'): void {
    if (action === 'update') {
      this.updateAsset();
      return;
    }

    this.saveAsset();
  }

  /**
   * create new asset method
   */
  public saveAsset(): void {
    this.lock = true;
    this.accountsService.createAccount(this.assetForm.value)
      .pipe(takeUntil(this.destroy$))
      .subscribe(response => {
        this.router.navigate(['/estate-plan/edit/'+ response.data.asset_unique_id]);
        this.toasterService.showSuccess('Account has been created');
        this.lock = false;
      }, () => {
        this.toasterService.showError('Something went wrong');
        this.lock = false;
      });
  }

  /**
   * update new asset method
   */
  public updateAsset(): void {
    this.lock = true;
    this.accountsService.updateAccount(this.assetForm.value, this.assetId)
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        this.toasterService.showSuccess('Account has been updated');
        this.lock = false;
      }, () => {
        this.toasterService.showSuccess('Something went wrong');
        this.lock = false;
      });
  }
}
