// 3rd party libs
import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { Subject } from "rxjs";
import { takeUntil } from "rxjs/operators";
import { MatDialog } from '@angular/material';

// services
import { HelpersService } from '../../../../core/services/helpers.service';
import { AccountsService } from '../../../../core/services/accounts.service';
import { ToasterService } from '../../../../services/toaster.service';

// components
import { AddAccountsPopupComponent } from '../../popups/add-accounts-popup/add-accounts-popup.component';

// models
import { AccountsClass } from 'src/app/core/models/Accounts';
import { IAccounts } from './../../../../core/models/IAccounts';

@Component({
  selector: 'app-estate-plan-page',
  templateUrl: './estate-plan-page.component.html',
  styleUrls: ['./estate-plan-page.component.scss']
})
export class EstatePlanPageComponent implements OnInit, OnDestroy {

  public lock = false;
  public loading = true;
  public tableTitles: string[] = ['#', 'Drawer', 'Account Name', 'Account User ID', 'URL', 'Notes', 'Beneficiaries', 'Actions'];
  public estatePlan: AccountsClass;
  private destroy$: Subject<boolean> = new Subject<boolean>();

  constructor(
    public helpers: HelpersService,
    public accountsService: AccountsService,
    public toasterService: ToasterService,
    private router: Router,
    private dialog: MatDialog,
  ) {
    this.estatePlan = this.accountsService.estatePlan;
  }

  ngOnInit() {
    this.getEstatePlan();
  }

  ngOnDestroy() {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  /**
   * get estate plan assets list
   */
  private getEstatePlan(): void {
    this.accountsService.getAccounts(true)
      .pipe(takeUntil(this.destroy$))
      .subscribe(response => {
        const accounts = this.convertToEstatePlanTable(response.data.assets);
        console.log(accounts);
        this.estatePlan.setAccounts(accounts);
        this.estatePlan.setEstatePlanData(
          response.data.isComplete, 
          response.data.lastUpdatedDate
        );

        this.loading = false;
      });
  }

  /**
   * table buttons controls
   */
  public newAsset(): void {
    this.router.navigate(['/estate-plan/new']);
  }

  /**
   * redirect to the edit account page
   */
  public editAsset(id: number): void {
    this.router.navigate([`/estate-plan/edit/${id}`]);
  }

  /**
   * redirect to assign beneficiaries to the bulk accounts page
   */
  public assignBeneficiaries(): void {
    this.router.navigate([`/estate-plan/assign-beneficiaries`]);
  }

  /**
   * Remove account from estate plan list 
   */
  public deleteAsset(accountId: number): void {
    let isRemoveAccount = confirm(`Account will be removed from Estate Plan. However, this Account will still remain in your portfolio and can be added back`);
    if (!isRemoveAccount) return;

    this.lock = true;

    this.accountsService.removeAccount(accountId)
    .pipe(takeUntil(this.destroy$))
      .subscribe(response => {
        this.estatePlan.setEstatePlanData(response.data.isComplete);
        this.estatePlan.removeAccount(accountId);
        this.lock = false;
        this.toasterService.showSuccess('Account has been removed from estate plan list successfully');
      });
  }

  /**
   * show popup with all accounts witch not belong to estate plan
   */
  public addAccounts(): void {
    this.dialog.open(AddAccountsPopupComponent, {
      panelClass: "popup-screen",
      disableClose: true,
      width: "600px",
      autoFocus: false
    });
  }
  
  public printInfo(): void {
    alert('Coming soon');
  }

  /**
   * convert data to estate plan form data
   */
  private convertToEstatePlanTable(accounts): IAccounts[] {
    return accounts.map(value => {
      const {
        asset_unique_id,
        asset_account_num,
        asset_group_title,
        asset_title,
        asset_url,
        asset_notes,
        beneficiaries
      } = value;

      return {
        asset_unique_id,
        asset_group_title,
        // image: '',
        asset_title,
        asset_account_num,
        asset_url,
        asset_notes,
        beneficiaries
      }
    });
  }

  /**
   * helpers
   */
  public keepOrder = (a, b) => {
    return a;
  };
}
