import { Router } from '@angular/router';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

// services
import { AccountsService } from '../../../../core/services';
import { BeneficiariesService } from '../../../../core/services/beneficiaries.service';

// models
import { AccountsClass } from 'src/app/core/models/Accounts';
import { BeneficiariesClass } from '../../../../core/models/BeneficiariesClass';


@Component({
  selector: 'app-assign-beneficiaries-page',
  templateUrl: './assign-beneficiaries-page.component.html',
  styleUrls: ['./assign-beneficiaries-page.component.scss']
})
export class AssignBeneficiariesPageComponent implements OnInit, OnDestroy {

  public lock = false;
  public chooseAllEvent;
  public accounts: AccountsClass;
  public beneficiaries: BeneficiariesClass;
  private destroy$: Subject<boolean> = new Subject<boolean>();

  constructor(
    private router: Router,
    private accountsService: AccountsService,
    private beneficiariesService: BeneficiariesService
  ) {
    this.accounts = this.accountsService.accounts;
    this.beneficiaries = this.beneficiariesService.beneficiaries;
  }

  ngOnInit() { }

  ngOnDestroy() {
    this.destroy$.next(true);
    this.destroy$.complete();
    this.beneficiaries.clearAll();
  }

  /**
   * add beneficiaries for bulk accounts
   */
  onChooseAll($event){
    this.chooseAllEvent.target.checked = false;
  }
  public assignBeneficiaries(): void {
    this.lock = true;

    const forSaving = {
      accounts: [],
      beneficiaries: []
    }

    forSaving.accounts = this.accounts
      .getCheckedAccounts().map(account => {
      return account.asset_unique_id
    });

    this.beneficiaries.getBeneficiaries()
      .forEach((beneficiary, index) => {
        forSaving.beneficiaries.push({
          contactId: beneficiary.user_caretaker_id,
          priority: index + 1
        })
      })

    this.beneficiariesService.getBulkBeneficiariesByAccounts(forSaving)
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        this.lock = false;
        this.router.navigate(['/estate-plan']);
      }, () => {
        this.lock = false;
      });
  }

}