import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { FormBuilder, FormGroup, FormArray } from '@angular/forms';
import { MatDialogRef } from '@angular/material';

// services
import { LegalService } from '../../../../services/legalService/legal.service';
import { BeneficiariesService } from '../../../../core/services/beneficiaries.service';
import { HelpersService } from '../../../../core/services/helpers.service';

// models
import { IContacts } from '../../../../core/models';
import { BeneficiariesClass } from '../../../../core/models/BeneficiariesClass';

@Component({
  selector: 'app-add-beneficiaries-popup',
  templateUrl: './add-beneficiaries-popup.component.html',
  styleUrls: ['./add-beneficiaries-popup.component.scss']
})
export class AddBeneficiariesPopupComponent implements OnInit {

  public contacts$: Observable<IContacts[]>;
  public contacts: IContacts[];
  public beneficiaries: BeneficiariesClass;
  public contactsForm: FormGroup;
  private newContacts: IContacts[] = [];


  constructor(
    public helpers: HelpersService,
    private fb: FormBuilder,
    public dialogRef: MatDialogRef<AddBeneficiariesPopupComponent>,
    private legalService: LegalService,
    private beneficiariesService: BeneficiariesService,
  ) {
    this.beneficiaries = this.beneficiariesService.beneficiaries;
  }

  ngOnInit() {
    this.getContacts();
  }

  /**
   * get all contacts from API
   */
  private getContacts(): void {
    this.contacts$ = this.legalService.getContacts().pipe(
      map(response => {
        console.log(response.data);
        this.contacts = response.data;
        console.log(this.contacts);
        this.createContactsForm();
        return response.data;
      })
    );
  }

  /**
   * create contacts form
   */
  private createContactsForm(): void {
    this.contactsForm = this.fb.group({
      contacts: this.buildContacts()
    });

    if (this.newContacts.length === 5) {
      this.crossAmount(true);
    } 
  }

  /**
   * get list of controls from contacts form
   */
  get contactControls() {
    return this.contactsForm.get('contacts') as FormArray;
  };

  /**
   * add contacts list to FormArray controls
   */
  private buildContacts() {
    const array = this.contacts.map(contact => {
      const isCheckedItem = this.beneficiaries.getBeneficiaries()
        .filter(e => e.user_caretaker_id === contact.user_caretaker_id).length > 0;

      if (isCheckedItem) {
        this.newContacts.push(contact);
      }

      return this.fb.control(isCheckedItem);
    });
    return this.fb.array(array);
  }

  /**
   * manage local contact list 
   */
  public onCheckboxChanges(event, contact): void {
    if (!event.target.checked) {
      const contacts = this.newContacts.filter(item => item.user_caretaker_id !== contact.user_caretaker_id);
      this.newContacts = contacts;
    }

    if (event.target.checked) {
      this.newContacts.push(contact);
    }

    if (this.newContacts.length === 5) {
      this.crossAmount(true);
    } 

    if (this.newContacts.length < 5) {
      this.crossAmount(false);
    } 
  }

  /**
   * disable/enable checkboxes
   */
  private crossAmount(isCrossAmount): void {
    this.contactControls.controls.map(control => {
      if (!isCrossAmount) {
        control.enable();
        return;
      }

      if (!control.value && isCrossAmount) {
        control.disable();
      }
    });
  }

  /**
   * add contact to beneficiaries list
   */
  public addToBeneficiaries(): void {
    this.beneficiaries.setBeneficiaries(this.newContacts);
    this.dialogRef.close();
  }

}
